
import { defineComponent, ref } from 'vue'

export default defineComponent({
  props: {
    title: {
      type: String,
      default: ''
    },
    propList: {
      type: Array,
      required: true
    },
    listData: {
      type: Array,
      required: true
    },

    childrenProps: {
      type: Object,
      default: () => ({})
    }
  },
  setup() {
    const queryInfo = ref({
      query: '',
      pageNum: 1,
      pageSize: 4
    })
    return {
      queryInfo
    }
  }
})
